import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: 'roundNum'
})

export class RoundPipe implements PipeTransform {
  transform(value: any, isUpper: any, addTo: any) {
    if(isUpper) {
      return Math.ceil(value * addTo)
    } else {
      return Math.floor(value * addTo)
    }
  }
}
