import { AfterViewInit, Component, ViewChild } from "@angular/core";

@Component({
  selector: 'sample',
  template: `
  <div *ngFor="let item of arr">
    <div #asd>{{item}}</div>
  </div>

  `
})

export class SampleComponent implements AfterViewInit {
  @ViewChild("asd") tref: any;

  dddd:any
  arr = [1,2,3,4]
  ngAfterViewInit() {
  this.tref.nativeElement.style.color = 'red'

  }
}
