import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";

@Component({
  selector: '<app-sign-in></app-sign-in>',
  template: `
  <form (ngSubmit)="onsubmit(formSignIn)" #formSignIn="ngForm">
    <input
    placeholder="Email"
    ngModel
    #txtEmail="ngModel"
    name="email"
    email
    required>
    <p *ngIf="txtEmail.errors?.required && txtEmail.touched">email is required</p>
    <p *ngIf="txtEmail.errors?.email && txtEmail.touched">email is not valid</p>
      <br><br>
      <input
      #txtPassword = "ngModel"
      name="password"
      type="password"
      placeholder="Password"
      ngModel
      minlength="3"
      pattern="[a-z]*"
      >
      <br><br>
      <button [disabled]="formSignIn.invalid">Submit</button>
      <br>
      <div ngModelGroup="gr">
        <label><input [ngModel]="true" type="checkbox" name="football">football</label>
        <label><input [ngModel]="true" type="checkbox" name="football">football</label>
      </div>
  </form>
  <br>
  <button (click)="postToExpress()">POST</button>
  <p>{{txtEmail.errors| json}}</p>
  <p>{{txtPassword.errors| json}}</p>
  <p>{{formSignIn.value| json}}</p>

  <h1>{{json}}</h1>
  `,
  styleUrls: ['./app.component.css']
})

export class SignInComponent {
  constructor(private http: HttpClient) { }

  onsubmit(formSignIn: { value: any; }) {
    console.log(formSignIn.value);
  }


  // postToExpress() {
  //   const url = 'http://http://localhost:3000/signin'
  //   const headers = new HttpHeaders()
  //   headers.append('Content-Type', 'application/json')
  //   const body = JSON.stringify({name: 'viet hung 2'})
  //   this.http.post<{text: any}>(url, body, {headers})
  //   .toPromise()
  //   .then(res => res.text())
  //   .then(restext => console.log(restext))
  // }

  postData = {
    name: 'my test'
  }
  url = `http://localhost:3000/signin`
  json: any;
  postToExpress() {
    this.http.post(this.url, this.postData)
      .toPromise()
      .then((res: any) => {
        console.log(res)
        this.json = (res)
      })
  }
  getmy() {
    this.http.get(this.url)
      .toPromise()
      .then((res: any) => {
        console.log(res)
        this.json = (res)
      })
  }
}
