import { Component, ViewChild } from '@angular/core';
import { WeatherService } from './weather.service';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent {
  @ViewChild('aaaaa') aaaaa: any
  getWeather: any
  isWeather = false
  myCity: any
  nameCity: any
  isLoading = false

  constructor(private weatherService: WeatherService) {
  }

  getWeatherFromService() {
    this.isLoading = true
    this.weatherService.getWeather(this.myCity)
      .then(res => this.getWeather = res)
      .catch(err => {
        this.isLoading = true
        this.isWeather = false
      })
      this.isLoading = false
  }

  myWeather() {
    this.getWeatherFromService()
    this.nameCity = this.myCity
    this.isWeather = true

  }

  getMessage() {
    if (this.isLoading) {
      return '..........'
    }
    let message = `${this.nameCity} is now ${this.getWeather}`
    return message
  }
}
