import { Component } from "@angular/core";

@Component({
  templateUrl: './urser-form.component.html',
  styleUrls: ['./user-form.component.css'],
  selector: '<app-user-form></app-user-form>'
})

export class UserFormComponent {
  name: string = ''
  hamKeyUp(event: any) {
    this.name = event.target.value
  }
  isHightLight = true

}
